import React, { Component } from "react";
import {Link} from "react-router-dom";
import {Menu} from "antd";

export default class MainNav extends Component {
    
    render (){
        let {mode,id} = this.props;
        return (
            <Menu mode={mode} theme="light" id={id}>
                <Menu.Item>
                    <Link to="/index/all"></Link>
                </Menu.Item>
                <Menu.Item>
                    <Link to="/index/good"></Link>
                </Menu.Item>
                <Menu.Item>
                    <Link to="/index/ask"></Link>
                </Menu.Item>
                <Menu.Item>
                    <Link to="/index/share"></Link>
                </Menu.Item>
                <Menu.Item>
                    <Link to="/index/job"></Link>
                </Menu.Item>
                <Menu.Item>
                    <Link to="/index/test"></Link>
                </Menu.Item>
            </Menu>
        )
    }
}



